import { Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { environment } from 'projects/browser-extension/src/environments/environment';
import { EUserMenuActions } from '../../enums/user-menu-actions.enum';
import { ExtAuthService } from '../../services/ext-auth.service';
import { IExtUser } from '@shared/interfaces';

@Component({
	selector: 'app-user-menu',
	templateUrl: './user-menu.component.html',
	styleUrls: ['./user-menu.component.scss'],
})
export class UserMenuComponent implements OnInit {
	@Input() user: IExtUser;
	@Input() isOrganizationMember: boolean;
	@ViewChild('actionsMenu', { static: false }) actionsMenu: ElementRef<HTMLInputElement>;

	get userEmail() {
		return this.user ? (this.user.emailAddress ? this.user.emailAddress : 'Guest') : '';
	}

	toggleMenu = false;
	eUserMenuActions = EUserMenuActions;
	constructor(private _authService: ExtAuthService) {}

	ngOnInit(): void {}

	async triggerMenuItemAction(menuItemClick: EUserMenuActions) {
		let tabURl: string;
		switch (menuItemClick) {
			case EUserMenuActions.MyAccount:
				tabURl = `${environment.identityWebsite}/personal-information`;
				break;
			case EUserMenuActions.Billing:
				tabURl = `${environment.identityWebsite}/billing`;
				break;
			case EUserMenuActions.TermsOfUse:
				tabURl = `https://copyleaks.com/legal/termsofuse`;
				break;
			case EUserMenuActions.HowDoWeKnow:
				tabURl = `https://help.copyleaks.com/kb-search-results?term=Ai+Content+detector`;
				break;
			case EUserMenuActions.LogOut:
				tabURl = `${environment.dashboardWebsite}/login-redirect?isLogout=true`;
				await this._authService.logoutAsync();
				break;
			default:
				return;
		}
		chrome.tabs.create({ url: tabURl });
	}

	@HostListener('document:click', ['$event'])
	clickout(event) {
		if (event?.target.parentElement?.parentElement?.parentElement?.id == 'menu-toggle-button') {
			return;
		}
		if (this.toggleMenu) {
			if (!this.actionsMenu?.nativeElement.contains(event.target)) {
				this.toggleMenu = false;
			}
		}
	}
}
