import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { IExtUserSubscriptionState } from '@ngx-common/models/ext-payment.model';
import { IPlatformProductsPlans } from '@ngx-common/models/pricing.model';
import { IUserBalance } from '@ngx-common/models/user-balance.models';
import { BehaviorSubject } from 'rxjs';
import { environment } from '../../environments/environment';

@Injectable({
	providedIn: 'root',
})
export class ExtPaymentService {
	private _userBalance$ = new BehaviorSubject<IUserBalance>(null);
	private _userSubscription$ = new BehaviorSubject<IExtUserSubscriptionState>(null);

	get userBalance$() {
		return this._userBalance$;
	}
	get userBalance() {
		return this._userBalance$.value;
	}

	get userSubscription$() {
		return this._userSubscription$;
	}
	get userSubscription() {
		return this._userSubscription$.value;
	}

	constructor(private _http: HttpClient) {}

	async getExtensionUserBalanceAsync(force = true): Promise<IUserBalance> {
		try {
			if (this.userBalance && !force) {
				return this.userBalance;
			}
			const balance = await this._http
				.get<IUserBalance>(`${environment.extensionAPI}/v1/account/balance?force=${force}`)
				.toPromise();
			this._userBalance$.next(balance);
			return this.userBalance;
		} catch (error) {
			throw error;
		}
	}

	async getExtensionSubscriptionStateAsync(): Promise<IExtUserSubscriptionState> {
		try {
			if (this.userSubscription) {
				return this.userSubscription;
			}
			return await this._http
				.get<IExtUserSubscriptionState>(`${environment.extensionAPI}/v1/account/balance/subscription`)
				.toPromise();
		} catch (error) {
			throw error;
		}
	}

	async getExtensionProductsPlansAsync(): Promise<IPlatformProductsPlans> {
		try {
			const result = await this._http
				.get<IPlatformProductsPlans>(`${environment.extensionAPI}/v1/pricing/plans`)
				.toPromise();
			return result;
		} catch (error) {
			throw error;
		}
	}

	async sendCreditsRequestToOwnerAsync() {
		try {
			return await this._http.post(`${environment.extensionAPI}/v1/credits-request-to-owner`, null).toPromise();
		} catch (error) {
			throw error;
		}
	}
}
