import { Component, Input, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { IUserBalance } from '@ngx-common/models/user-balance.models';
import { ExtPaymentService } from '../../services/ext-payment.service';
import { EPaymentPlanType } from '@ngx-common/enums/payment-plan-type.enum';
import { IExtUserSubscriptionState } from '@ngx-common/models/ext-payment.model';
import { NumeralPipe } from '@ngx-common/pipes/numeral/numeral.pipe';

@UntilDestroy(this)
@Component({
	selector: 'app-user-plan',
	templateUrl: './user-plan.component.html',
	styleUrls: ['./user-plan.component.scss'],
})
export class UserPlanComponent implements OnInit {
	userBalance: IUserBalance;
	userSubscription: IExtUserSubscriptionState;
	isLoading: boolean = true;
	pagesLeft: number;
	planPages: number;
	canShowTag: boolean;

	get pagesLeftAsString() {
		return this._numeralPipe.transform(this.pagesLeft, 1);
	}

	get pagesLeftPercentage() {
		return (this.pagesLeft / this.planPages) * 100;
	}

	get userPlanType() {
		switch (this.userBalance?.accountType) {
			case EPaymentPlanType.Trial:
				return 'Trial';
			case EPaymentPlanType.Prepaid:
				return 'Prepaid';
			case EPaymentPlanType.SharedAccount:
				return `Org Member`;
		}
	}

	get canShowCredits() {
		if (
			this.userBalance?.accountType == EPaymentPlanType.Subscription ||
			this.userBalance?.accountType == EPaymentPlanType.Prepaid ||
			this.userBalance?.accountType == EPaymentPlanType.Trial
		) {
			if (this.userBalance?.current == 0) {
				return false;
			}
			return true;
		}
		return false;
	}

	constructor(private _paymentService: ExtPaymentService, private _numeralPipe: NumeralPipe) {}

	ngOnInit(): void {
		this._paymentService.userBalance$.pipe(untilDestroyed(this)).subscribe(async balance => {
			if (balance) {
				this.userBalance = balance;
				this.pagesLeft = balance.current;
				this.planPages = balance.max;
				this._initSubscription();
				this.isLoading = false;
				document.documentElement.style.setProperty('--progress-bar-percentage', this.pagesLeftPercentage + `%`);
				if (this.pagesLeftPercentage < 15) {
					document.documentElement.style.setProperty('--progress-bar-color', '#f44336');
				} else {
					document.documentElement.style.setProperty('--progress-bar-color', '#3f9af5');
				}
			}
		});
	}

	async _initSubscription() {
		if (
			this.userBalance.accountType == EPaymentPlanType.Subscription ||
			this.userBalance.accountType == EPaymentPlanType.Trial
		) {
			this.canShowTag = false;
			if (this.userBalance.accountType == EPaymentPlanType.Subscription) {
				this.userSubscription = await this._paymentService.getExtensionSubscriptionStateAsync();
			}
		} else {
			this.canShowTag = true;
		}
	}
}
