import { Component, Input, OnInit } from '@angular/core';
import { IExtUser } from '@shared/interfaces';

@Component({
	selector: 'app-ext-header',
	templateUrl: './ext-header.component.html',
	styleUrls: ['./ext-header.component.scss'],
})
export class ExtHeaderComponent implements OnInit {
	@Input() user: IExtUser;
	@Input() isOrganizationMember: boolean;

	constructor() {}

	ngOnInit(): void {}

	goToLink() {
		let tabURl = 'https://copyleaks.com';
		chrome.tabs.create({ url: tabURl });
	}
}
